body{
  background-color:grey;
}

.App{
  background-color:lightgrey;
  min-height: 500px;
  width:400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color:lightslategrey;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;

}

#to-do-form input::placeholder{
  color:black;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 0px;
  margin: 20px;
}

#to-do-form input:focus{
  outline: none;
}

#to-do-form button{
  height:50px;
  width:80px;
  border-radius:10px;
  border: 0;
  background-color: darkgray;
  cursor: pointer;
  font-weight: bold;
  color:black;
}

#to-do-form button:focus{
  outline: none;
}